<template>
  <div class="header">
    <div class="header-logo">
      <a href="">
        <span class="word-prefix">O</span>
        <span class="word-suffix">OLCHINA</span>
      </a>
    </div>
    <div :class="`${prefix}_header-menu`">
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">资讯</el-menu-item>
        <el-menu-item index="2">专区</el-menu-item>
        <el-menu-item index="3">问答</el-menu-item>
        <el-menu-item index="4">活动</el-menu-item>
        <el-menu-item index="5">软件库</el-menu-item>
        <el-menu-item index="6">发现</el-menu-item>
        <el-menu-item index="7">博客</el-menu-item>
        <el-menu-item index="8">动弹</el-menu-item>
        <el-menu-item index="9">Gitee</el-menu-item>
      </el-menu>
    </div>
    <div class="header-search">
      <el-form :inline="true" :model="searchForm" class="searchForm">
        <el-form-item class="single-input">
          <el-input placeholder="请输入内容" v-model="searchForm.question">
            <template slot="prepend">Http://</template>
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="login-or-register">
      <a href="">登录</a>
      <a href="">注册</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'osChinaHeader',
  data () {
    return {
      prefix: 'os-china-header-prefix',
      searchForm: {
        question: ''
      }
    }
  },
  methods: {
    handleSelect () {
    }
  }
}
</script>

<style lang="scss" scoped>
$header-height: 60px;

.header {
  height: $header-height;
  display: flex;
  box-sizing: border-box;
  justify-content: center;
  border-bottom: solid 1px #e6e6e6;

  & > > > .el-menu-item {
    font-size: 16px;
  }

  .header-logo {
    a {
      text-decoration: none;
      display: flex;
      align-items: baseline;
      box-sizing: border-box;
      padding: 5px;

      .word-prefix {
        margin: auto;
        border-radius: 50%;
        background-color: rgb(33, 179, 81);
        color: #fff;
        display: inline-block;
        height: 40px;
        width: 40px;
        line-height: 40px;
        font-size: 30px;
        text-align: center;
        font-weight: 700;
      }

      .word-suffix {
        display: inline-block;
        font-weight: 700;
        letter-spacing: 1px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        margin-left: 0.5rem;
      }

    }
  }

  .header-search {
    .searchForm {
      height: 100%;
      display: flex;
      align-items: baseline;
      margin-left: 1rem;
    }

    .single-input {
      margin: auto;

    }
  }

  .login-or-register {
    line-height: 60px;
    padding-left: 3rem;

    a {
      text-decoration: none;
      color: #909399;
      margin-right: 0.2rem;
      font-size: 14px;
    }
  }
}
</style>



