<template>
  <div class="os-china-main-prefix">
    <div class="header">
      <os-china-header></os-china-header>
    </div>
    <div :class="`${prefix}_main`">
      <router-view></router-view>
    </div>
    <div :class="`${prefix}_footer`">
      footer
    </div>
  </div>
</template>

<script>
import osChinaHeader from './header/os-china-header'
export default {
  name: 'osChinaMain',
  components: {
    osChinaHeader
  },
  data () {
    return {
      prefix: 'os-china-main-prefix'
    }
  }
}
</script>

<style lang="scss" scoped>
$footer-height: 225px;

.os-china-main-prefix {

  &_footer {
    height: $footer-height;
    bottom: 0;
  }
}
</style>



